import { empty, end, start } from "bbcode-tags";
import { unlines } from "lines-unlines";
import { insertAtTheEnd, renderIn } from "~src/operations/logic/render";
import * as SITE from "~src/site";
import * as T from "~src/text";
import { toolbarButton } from "./logic/editing-tools";
import { indent, insert } from "./logic/textarea";
var Headings;
(function (Headings) {
    Headings[Headings["NONE"] = 0] = "NONE";
    Headings[Headings["FIRST"] = 1] = "FIRST";
    Headings[Headings["ALL"] = 2] = "ALL";
})(Headings || (Headings = {}));
const TABLE_CONFIG = { rows: 4, columns: 3, headings: true };
const tableToolbarButton = toolbarButton({
    action: (textarea, undoSupport) => insert(table(TABLE_CONFIG))(textarea, undoSupport),
    class: SITE.CLASS.toolbarTableButton,
    tooltip: T.general.tooltip_table,
});
export default (undoSupport) => (e) => {
    const parent = e.unorderedListButton.parentElement;
    renderIn(parent, insertAtTheEnd, tableToolbarButton(e.textarea, undoSupport));
};
function table(config) {
    const headingRow = tableRowLines(config.columns, 2 /* ALL */);
    const regularRow = tableRowLines(config.columns, config.headings ? 1 /* FIRST */ : 0 /* NONE */);
    const theadLines = concat([
        start("thead"),
        headingRow.map(indent),
        end("thead"),
    ]);
    const tbodyLines = concat([
        start("tbody"),
        concat(replicate(config.rows - 1, regularRow.map(indent))),
        end("tbody"),
    ]);
    const tableLines = concat([
        start("table"),
        theadLines.map(indent),
        tbodyLines.map(indent),
        end("table"),
    ]);
    return unlines(tableLines);
}
function tableRowLines(columns, headings) {
    const cell_first = headings === 0 /* NONE */ ? "td" : "th";
    const cell_rest = headings === 2 /* ALL */ ? "th" : "td";
    return concat([
        start("tr"),
        indent(empty(cell_first)),
        replicate(columns - 1, indent(empty(cell_rest))),
        end("tr"),
    ]);
}
// From Haskell. Example: replicate(3, 42) = [42, 42, 42]
function replicate(n, x) {
    return Array(n).fill(x);
}
function concat(xs) {
    return [].concat(...xs);
}
