import { h } from "preact";
import * as CONFIG from "~src/config";
import { insertAtTheBeginning, renderIn } from "~src/operations/logic/render";
import * as SITE from "~src/site";
import * as T from "~src/text";
export default (e) => {
    renderIn(e.notificationsBar, insertAtTheBeginning, (
    // Derived from the other links in the notifications bar.
    h("a", { href: CONFIG.PATH.PREFERENCES.link(SITE.PATH.SETTINGS.link), title: T.preferences._.title, class: "margin-medium-right" },
        h("svg", { class: "icon", dangerouslySetInnerHTML: { __html: SITE.ICONS.settings } }),
        h("span", { class: "margin-small-left display-none display-l-inherit" }, T.preferences._.shortcut_label))));
};
