import * as Mousetrap from "mousetrap";
import { Action } from "~src/actions";
import { clickOn } from "~src/operations/logic/click";
import { P, Preferences } from "~src/preferences";
import * as SITE from "~src/site";
import { insertIn, wrap_tag } from "../logic/textarea";
const keyboardShortcuts = Preferences.get(P.keyboard);
export function submit(e) {
    bindKeyboardShortcut(Action.SUBMIT, _ => clickOn(e.saveButton), { preventDefault: true });
}
export function preview(e) {
    bindKeyboardShortcut(Action.PREVIEW, _ => clickOn(e.previewButton), { preventDefault: true });
}
export const insertLink = (undoSupport) => (e) => {
    bindKeyboardShortcut(Action.INSERT_LINK, _ => wrap_tag({ tag: SITE.TAG.url, parameterized: true, block: false })(e.textarea, undoSupport), { preventDefault: true });
};
export const insertTab = (content, undoSupport) => (e) => {
    bindKeyboardShortcut(Action.INSERT_TAB, event => {
        if (document.activeElement === e.textarea) {
            event.preventDefault();
            insertIn(e.textarea, { string: content, replace: undoSupport });
        }
    }, { preventDefault: false });
};
function bindKeyboardShortcut(action, handler, options) {
    for (const entry of keyboardShortcuts) {
        if (entry.action === action) {
            Mousetrap.bind(entry.shortcut, event => {
                if (options.preventDefault) {
                    event.preventDefault();
                }
                handler(event);
            });
        }
    }
}
