export const r = String.raw;
export function fromMaybeUndefined(fallback, x) {
    return x === undefined ? fallback : x;
}
export function truncate(maxTotalLength, s) {
    const REPLACEMENT = "[]";
    return (s.length > maxTotalLength
        ? s.slice(0, maxTotalLength - REPLACEMENT.length) + REPLACEMENT
        : s);
}
export function withMaybe(ma, f) {
    if (ma !== null) {
        f(ma);
    }
}
export function yyyymmdd(date) {
    const yyyy = date.getFullYear();
    const mm = (date.getMonth() + 1 /* 0-indexed */).toString().padStart(2, "0");
    const dd = date.getDate().toString().padStart(2, "0");
    return yyyy + mm + dd;
}
export function assertExhausted(x) {
    throw new Error(`assertExhausted: ${x}`);
}
