import unique from "array-uniq";
import { h } from "preact";
import * as CONFIG from "~src/config";
import { mention } from "~src/operations/edit-mode";
import { insertBefore, renderIn } from "~src/operations/logic/render";
import SELECTOR from "~src/selectors";
import * as SITE from "~src/site";
import * as T from "~src/text";
export default (e) => {
    const button = mentionEveryoneButton({
        message: usersToMention(e.forumPostContainer).map(mention).join(" "),
        replyURL: e.quickReplyForm.getAttribute("action") || "",
        csrf: session.getCsrfToken(),
    });
    if (document.querySelector(`${SELECTOR.forumPost} ${SELECTOR.forumPostAuthorLink}`) === null) {
        // Broken thread participant detection.
        return "Could not detect thread participants";
    }
    renderIn(e.replyButton.parentElement, insertBefore(e.replyButton), button);
};
function usersToMention(forumPostContainer) {
    const postBySomeoneElse = SELECTOR.forumPost + `:not(.${SITE.CLASS.forumPostByCurrentUser})`;
    const selector = `${postBySomeoneElse} ${SELECTOR.forumPostAuthorLink}`;
    const nameLinks = Array.from(forumPostContainer.querySelectorAll(selector));
    return unique(nameLinks.map(e => e.textContent.trim()));
}
function mentionEveryoneButton(props) {
    return (h("form", { method: "POST", action: props.replyURL, class: CONFIG.CLASS.mentionEveryoneButton },
        h("input", { name: SITE.FORM.name.message, type: "hidden", value: props.message }),
        h("input", { name: SITE.FORM.name.csrfToken, type: "hidden", value: props.csrf }),
        h("input", { name: SITE.FORM.name.action, type: "hidden", value: SITE.FORM.value.preview }),
        h("button", { type: "submit", class: SITE.CLASS.button, title: T.general.mention_everyone_tooltip }, T.general.mention_everyone_label)));
}
