import { Component, h, render } from "preact";
import * as Storage from "ts-storage";
import * as CONFIG from "~src/config";
import * as T from "~src/text";
export function logger(regularLogger, rootNode) {
    const initOpen = Storage.get_session(CONFIG.KEY.developer_tools_open, false).value;
    let devTools;
    render(h(DevTools, { initOpen: initOpen, ref: self => { devTools = self; } }), rootNode);
    return Object.assign(Object.assign({}, regularLogger), { error: (prefix, message) => {
            devTools.log({ kind: "error", content: message });
            regularLogger.error(prefix, message);
        }, warn: (prefix, message) => {
            devTools.log({ kind: "warning", content: message });
            regularLogger.warn(prefix, message);
        } });
}
class DevTools extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loggedMessages: [],
            isOpen: props.initOpen,
        };
    }
    log(message) {
        this.setState(state => ({
            loggedMessages: [...state.loggedMessages, message],
        }));
    }
    render() {
        const loggedMessages = this.state.loggedMessages;
        const loggedWarnings = loggedMessages.filter(msg => msg.kind === "warning");
        const loggedErrors = loggedMessages.filter(msg => msg.kind === "error");
        return (h("footer", { id: CONFIG.ID.developerTools, class: this.state.isOpen ? "open" : undefined, "data-how-many": loggedMessages.length },
            h("a", { onClick: _ => {
                    this.setState(state => {
                        const newOpenState = !state.isOpen;
                        Storage.set_session(CONFIG.KEY.developer_tools_open, newOpenState);
                        return Object.assign(Object.assign({}, state), { isOpen: newOpenState });
                    });
                } },
                summary(loggedErrors.length, "error"),
                summary(loggedWarnings.length, "warning")),
            h("ol", null, loggedMessages.length > 0
                ? loggedMessages.map(msg => h("li", { class: CONFIG.CLASS.developerTools[msg.kind] }, msg.content))
                : h("li", null, T.developer_mode.nothing_logged))));
    }
}
function summary(howMany, what) {
    const icon = {
        error: "",
        warning: "",
    }[what];
    return (h("strong", { "data-how-many": howMany, class: CONFIG.CLASS.developerTools[what], title: T.developer_mode.tooltip[what](howMany) },
        icon,
        "\u00A0\u00A0",
        howMany));
}
