import classNames from "classnames";
import { h } from "preact";
import * as CONFIG from "~src/config";
import { insertAtTheEnd, renderIn } from "~src/operations/logic/render";
import * as SITE from "~src/site";
import * as T from "~src/text";
export default (e) => {
    renderIn(e.settingsNavigation, insertAtTheEnd, (h("li", { title: T.general.preferences_link, class: SITE.CLASS.menuItem },
        h("a", { href: CONFIG.PATH.PREFERENCES.link(SITE.PATH.SETTINGS.link), class: classNames(SITE.CLASS.button, SITE.CLASS.link) },
            h("span", { class: SITE.CLASS.icon }),
            " ",
            " ",
            h("span", { class: SITE.CLASS.label }, T.general.preferences_link)))));
};
